.bind-phone {
  width: 100%;
  height: 100%;
  background: url(../../static/media/bg.f1e4472b.png) no-repeat left center;
  background-size: cover;
}
.bind-phone .logo {
  text-align: center;
  padding: 0.5rem 0 0.3rem;
}
.bind-phone .logo img {
  width: 0.8rem;
  margin-bottom: 0.23rem;
}
.bind-phone .logo p {
  line-height: 0.28rem;
  color: #333333;
  font-size: 0.2rem;
}
.bind-phone .get-code .am-list-item {
  padding-right: 0.15rem;
}
.bind-phone .get-code .code {
  width: 1.85rem;
}
.bind-phone .get-code .code .am-input-control input {
  display: inline-block;
  width: 1.8rem;
}
.bind-phone .get-code .btn {
  width: 1.07rem;
  height: 0.5rem;
  border-radius: 0.04rem;
  background-color: #dba131;
  line-height: 0.5rem;
  font-size: 0.16rem;
  text-align: center;
}
.bind-phone input {
  text-indent: 0.15rem;
}
.bind-phone input::-webkit-input-placeholder {
  height: 0.22rem;
  color: #999999;
  font-size: 0.16rem;
}
.bind-phone input::-moz-placeholder {
  height: 0.22rem;
  color: #999999;
  font-size: 0.16rem;
}
.bind-phone input:-ms-input-placeholder {
  height: 0.22rem;
  color: #999999;
  font-size: 0.16rem;
}
.bind-phone input::-ms-input-placeholder {
  height: 0.22rem;
  color: #999999;
  font-size: 0.16rem;
}
.bind-phone input::placeholder {
  height: 0.22rem;
  color: #999999;
  font-size: 0.16rem;
}
.bind-phone .am-list-item {
  padding: 0 0.33rem;
}
.bind-phone .am-list-item.am-input-item {
  height: auto;
}
.bind-phone .am-list-item .am-list-line {
  overflow: visible;
}
.bind-phone .am-list-item .am-list-line::after {
  display: none !important;
}
.bind-phone .am-list-item .am-input-control input {
  width: 3.1rem;
  height: 0.5rem;
  border-radius: 0.04rem;
  border: 0.01rem solid #dddddd;
  margin: 0 auto;
  margin-bottom: 0.2rem;
}
.bind-phone .bind-btn {
  width: 3.1rem;
  height: 0.5rem;
  border-radius: 0.04rem;
  background-color: #dba131;
  line-height: 0.5rem;
  color: #ffffff;
  font-size: 0.16rem;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 0.2rem;
}
.bind-phone .bind-tips {
  padding: 0 0.32rem;
  line-height: 0.22rem;
  color: #333333;
  font-size: 0.14rem;
}
.change-phone {
  width: 100%;
  height: 100%;
  background: url(../../static/media/bg.f1e4472b.png) no-repeat left center;
  background-size: cover;
}
.change-phone .logo {
  text-align: center;
  padding: 0.5rem 0 0.3rem;
}
.change-phone .logo img {
  width: 0.8rem;
  margin-bottom: 0.23rem;
}
.change-phone .logo p {
  line-height: 0.28rem;
  color: #333333;
  font-size: 0.2rem;
}
.change-phone .show-phone {
  width: 3.1rem;
  height: 0.5rem;
  border-radius: 0.04rem;
  border: 0.01rem solid #dddddd;
  line-height: 0.5rem;
  color: #333333;
  font-size: 0.16rem;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 0.3rem;
}
.change-phone .bind-btn {
  width: 3.1rem;
  height: 0.5rem;
  border-radius: 0.04rem;
  background-color: #dba131;
  line-height: 0.5rem;
  color: #ffffff;
  font-size: 0.16rem;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 0.2rem;
}
.change-phone .bind-tips {
  padding: 0 0.32rem;
  line-height: 0.22rem;
  color: #333333;
  font-size: 0.14rem;
}
